import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';

import { ItemModel } from '@backbase/foundation-ang/core';
import { AccountStatementsPostRequest } from '@backbase/data-ang/account-statements';

import { secondsToMilliseconds } from '../helpers/mappers';
import { PaginationType } from './pagination-type.model';
import { SortDirection } from './sort-direction.model';
import { modelItemToBoolean, toInteger } from '../helpers/utils';
import { Optional } from '@angular/core';

export const DEFAULT_NOTIFICATION_DISMISS_TIME = 5;
export const DEFAULT_PAGE_SIZE = 24;

export const widgetPropertyNames = {
  /* eslint-disable @typescript-eslint/naming-convention */
  PAGE_SIZE: 'bb.accountStatement.pageSize',
  SORTING_ORDER: 'bb.accountStatement.sortingOrder',
  SORTING_DIRECTION: 'bb.accountStatement.sortingDirection',
  RELATIVE_DATE_LIMIT: 'bb.accountStatement.relativeDateLimit',
  ABSOLUTE_DATE_LIMIT: 'bb.accountStatement.absoluteDateLimit',
  NOTIFICATION_DISMISS_TIME: 'notificationDismissTime',
  SHOW_CATEGORY: 'bb.accountStatement.showCategory',
  /* eslint-enable @typescript-eslint/naming-convention */
};

export abstract class AccountStatementBasePropertiesService {
  /* eslint-disable @typescript-eslint/member-ordering */
  private readonly pageSize$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly sortingOrder$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly sortingDirection$$ = new BehaviorSubject<SortDirection | undefined>(undefined);
  private readonly relativeDateLimit$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly absoluteDateLimit$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly paginationType$$ = new BehaviorSubject<PaginationType | undefined>(undefined);
  private readonly notificationDismissTime$$ = new BehaviorSubject<number | undefined>(undefined);
  private readonly showCategory$$ = new BehaviorSubject<boolean | undefined>(undefined);

  protected constructor(@Optional() protected readonly itemModel: ItemModel) {}

  readonly pageSize$ = this.getWidgetProperty$(this.pageSize$$, this.pageSizeIM$);
  readonly sortingOrder$ = this.getWidgetProperty$(this.sortingOrder$$, this.sortingOrderIM$);
  readonly sortingDirection$ = this.getWidgetProperty$(this.sortingDirection$$, this.sortingDirectionIM$);
  readonly relativeDateLimit$ = this.getWidgetProperty$(this.relativeDateLimit$$, this.relativeDateLimitIM$);
  readonly absoluteDateLimit$ = this.getWidgetProperty$(this.absoluteDateLimit$$, this.absoluteDateLimitIM$);
  readonly paginationType$ = this.getWidgetProperty$(this.paginationType$$, this.getPaginationTypeIM$());
  readonly notificationDismissTime$ = this.getWidgetProperty$(
    this.notificationDismissTime$$,
    this.notificationDismissTimePropertyIM$,
  ).pipe(map(secondsToMilliseconds));
  readonly showCategory$ = this.getWidgetProperty$(this.showCategory$$, this.showCategoryIM$);
  /* eslint-enable @typescript-eslint/member-ordering */

  setPaginationType(value: PaginationType | undefined): void {
    this.paginationType$$.next(value);
  }

  setPageSize(value: number | undefined): void {
    this.pageSize$$.next(value);
  }

  setSortingOrder(value: string | undefined): void {
    this.sortingOrder$$.next(value);
  }

  setSortingDirection(value: SortDirection | undefined): void {
    this.sortingDirection$$.next(value);
  }

  setNotificationDismissTime(value: number | undefined): void {
    this.notificationDismissTime$$.next(value);
  }

  setRelativeDateLimit(value: number | undefined): void {
    this.relativeDateLimit$$.next(value);
  }

  setAbsoluteDateLimit(value: string | undefined): void {
    this.absoluteDateLimit$$.next(value);
  }

  setShowCategory(value: boolean | undefined): void {
    this.showCategory$$.next(value);
  }

  protected getWidgetProperty$<T>(property$: Observable<T | undefined>, propertyIM$: Observable<T>): Observable<T> {
    return combineLatest([property$, propertyIM$]).pipe(
      map(([value, valueIM]) => (value === undefined ? valueIM : value)),
    );
  }

  protected abstract getPaginationTypeIM$(): Observable<PaginationType>;

  private get pageSizeIM$(): Observable<number> {
    const defaultValue = DEFAULT_PAGE_SIZE;
    return (
      this.itemModel?.property(widgetPropertyNames.PAGE_SIZE, defaultValue).pipe(map(toInteger)) || of(defaultValue)
    );
  }

  private get sortingOrderIM$(): Observable<string> {
    const defaultValue = 'date';
    return this.itemModel?.property(widgetPropertyNames.SORTING_ORDER, defaultValue) || of(defaultValue);
  }

  private get sortingDirectionIM$(): Observable<SortDirection> {
    const defaultValue = AccountStatementsPostRequest.DirectionEnum.DESC;
    return this.itemModel?.property(widgetPropertyNames.SORTING_DIRECTION, defaultValue) || of(defaultValue);
  }

  private get relativeDateLimitIM$(): Observable<number> {
    const defaultValue = 0;
    return (
      this.itemModel?.property(widgetPropertyNames.RELATIVE_DATE_LIMIT, defaultValue).pipe(map(toInteger)) ||
      of(defaultValue)
    );
  }

  private get absoluteDateLimitIM$(): Observable<string> {
    const defaultValue = '';
    return this.itemModel?.property(widgetPropertyNames.ABSOLUTE_DATE_LIMIT, defaultValue) || of(defaultValue);
  }

  private get notificationDismissTimePropertyIM$(): Observable<number> {
    const defaultValue = DEFAULT_NOTIFICATION_DISMISS_TIME;
    return (
      this.itemModel?.property<number>(widgetPropertyNames.NOTIFICATION_DISMISS_TIME, defaultValue).pipe(
        map(Number),
        map(num => (isNaN(num) ? DEFAULT_NOTIFICATION_DISMISS_TIME : num)),
      ) || of(defaultValue)
    );
  }

  private get showCategoryIM$(): Observable<boolean> {
    const defaultValue = true;
    return (
      this.itemModel?.property(widgetPropertyNames.SHOW_CATEGORY, defaultValue).pipe(map(modelItemToBoolean)) ||
      of(defaultValue)
    );
  }
}
