import { ChangeDetectionStrategy, Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { AccountStatementDocument } from '@backbase/data-ang/account-statements';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { MimeTypeExtensions } from '../../models/mime-type-extensions.model';

@Component({
  selector: 'bb-account-statement-downloads',
  templateUrl: './account-statement-downloads.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountStatementDownloadsComponent {
  @Input() documents: AccountStatementDocument[] = [];

  @Input() mimeTypeExtensions: MimeTypeExtensions = {};

  @Output() downloadFailure = new EventEmitter<Error>();

  @Input() describedByElementId = '';

  /**
   * Position where tooltip will be displayed relative to the button element.
   *
   * @default 'top'
   */
  @Input() tooltipPlacement = 'top';

  onDownloadDocFailure(error: Error) {
    this.downloadFailure.emit(error);
  }
}

@Directive({
  selector: '[bbAccountStatementDownloadsCustomizableDirective]',
})
export class AccountStatementDownloadsCustomizableDirective extends BbTemplate<
  AccountStatementDownloadsComponent,
  {
    documents: AccountStatementDocument[];
    mimeTypeExtensions: MimeTypeExtensions;
  }
> {}
