import { Directive, EventEmitter, HostListener, Input, Output, Renderer2 } from '@angular/core';
import { AccountStatementDocument } from '@backbase/data-ang/account-statements';

import { AccountStatementDomService } from '../../services/account-statement-dom-service';
import { isQueryStringExist } from '../../helpers/utils';

// @dynamic to suppress error with resolving Window type during compilation
@Directive({
  selector: '[bbAccountStatementDownloadDoc]',
})
export class AccountStatementDownloadDocDirective {
  /**
   * document item which is available for downloading
   */
  @Input('bbAccountStatementDownloadDoc') doc: AccountStatementDocument = {};

  /**
   * custom filename for document
   */
  @Input('bbAccountStatementDownloadDocFilename') filename = '';

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('bbAccountStatementDownloadDocFailure') downloadFailure = new EventEmitter<Error>();

  constructor(private readonly domService: AccountStatementDomService, private readonly renderer: Renderer2) {}

  @HostListener('click')
  @HostListener('keydown.enter')
  @HostListener('keydown.space')
  onClick() {
    this.downloadDoc(this.doc);
  }

  private downloadDoc(doc: AccountStatementDocument) {
    if (doc.url) {
      const url = isQueryStringExist(doc.url) ? `${doc.url}&inline=false` : `${doc.url}?inline=false`;
      this.domService.simulateLinkClick(
        {
          href: url,
        },
        this.renderer,
      );
    }
  }
}
