import { formatDate } from '@angular/common';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Inject, Injectable, LOCALE_ID } from '@angular/core';
import {
  AccountStatement,
  AccountStatementHttpService,
  PostAccountStatementsRequestParams,
} from '@backbase/data-ang/account-statements';

import { ProductSummaryHttpService, ProductSummaryItem } from '@backbase/data-ang/arrangements';

import { Observable, of, throwError } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { EMPTY_RESPONSE_ERROR } from '../constants/errors';

import {
  AccountStatementGetReqParams,
  Categories,
  ProccessedAccountStatementsRawData,
} from '../models/account-statement';
import { AccountsGetRequestParams } from '../models/accounts.model';
import { HttpObserveType } from '../models/http-observe.model';
import { AccountItem, Accounts } from '../models/product-summary';

const getArrangementsRequiredParams = {
  businessFunction: 'Product Summary',
  resourceName: 'Product Summary',
  privilege: 'view',
};

export interface NonNullableHttpResponse<T> extends HttpResponse<T> {
  body: T;
}
const isNonNullableHttpResponse = <T>(response: HttpResponse<T>): response is NonNullableHttpResponse<T> =>
  // eslint-disable-next-line no-null/no-null
  response.body !== null;

@Injectable()
export class AccountStatementDataService {
  constructor(
    private readonly productSummaryHttpService: ProductSummaryHttpService,
    private readonly accountStatementsHttpService: AccountStatementHttpService,
    private readonly http: HttpClient,
    @Inject(LOCALE_ID) private readonly locale: string,
  ) {}

  getAccounts(params?: AccountsGetRequestParams): Observable<NonNullableHttpResponse<ProductSummaryItem[]>> {
    return this.productSummaryHttpService
      .getArrangementsByBusinessFunction(
        {
          ...getArrangementsRequiredParams,
          favoriteFirst: true as any,
          ...params,
        },
        HttpObserveType.RESPONSE,
      )
      .pipe(
        switchMap(response => (isNonNullableHttpResponse(response) ? of(response) : throwError(EMPTY_RESPONSE_ERROR))),
      );
  }

  getAccountStatementsBusiness(
    params: PostAccountStatementsRequestParams,
  ): Observable<NonNullableHttpResponse<AccountStatement[]>> {
    return this.accountStatementsHttpService
      .postAccountStatements(params, HttpObserveType.RESPONSE)
      .pipe(
        switchMap(response =>
          isNonNullableHttpResponse(response) && Array.isArray(response.body)
            ? of(response)
            : throwError(EMPTY_RESPONSE_ERROR),
        ),
      );
  }

  loadAccounts(): Observable<Accounts | null> {
    return this.productSummaryHttpService
      .getArrangementsByBusinessFunction(getArrangementsRequiredParams, HttpObserveType.RESPONSE)
      .pipe(map(response => this.processAccountsRawData(response.body)));
  }

  loadCategories(): Observable<Categories> {
    return this.accountStatementsHttpService.getCategories(HttpObserveType.RESPONSE).pipe(
      map(({ body }) => {
        // eslint-disable-next-line no-null/no-null
        if (body === null) {
          throw new Error('Response does not contain body');
        }
        return body.categories;
      }),
    );
  }

  loadAccountStatements(params: AccountStatementGetReqParams = {}): Observable<ProccessedAccountStatementsRawData> {
    return this.accountStatementsHttpService
      .getAccountStatements(this.formatAccountStatementsParams(params), HttpObserveType.RESPONSE)
      .pipe(map((response: HttpResponse<AccountStatement[]>) => this.processAccountStatementsRawData(response)));
  }

  getAccountStatementDownloadUrl(uid: string) {
    return this.accountStatementsHttpService.downloadAccountStatementsUrl({ uid });
  }

  downloadFile(url: string): Observable<HttpResponse<ArrayBuffer>> {
    return this.http.get(url, {
      responseType: 'arraybuffer',
      observe: 'response',
    });
  }

  private processAccountsRawData(rawData: Array<ProductSummaryItem> | null): Accounts | null {
    return (
      rawData &&
      rawData.map(
        (account: ProductSummaryItem) =>
          ({
            id: account.id,
            name: account.bankAlias || account.name,
            identifier: account.IBAN || account.BBAN || account.productNumber,
            amount: account.availableBalance,
            currency: account.currency,
          } as AccountItem),
      )
    );
  }

  private processAccountStatementsRawData(
    rawData: HttpResponse<AccountStatement[]> | null,
  ): ProccessedAccountStatementsRawData {
    const totalCount: number = (rawData && parseInt(String(rawData.headers.get('x-total-count')), 10)) || 0;

    return {
      data: rawData && rawData.body,
      totalCount,
    };
  }

  private formatBookDate(date: string) {
    return formatDate(new Date(date), 'yyyy-MM-dd', this.locale);
  }

  private formatAccountStatementsParams(params: AccountStatementGetReqParams): AccountStatementGetReqParams {
    const { dateFrom, dateTo } = params;
    return {
      ...params,
      ...(dateFrom && {
        dateFrom: this.formatBookDate(dateFrom),
      }),
      ...(dateTo && {
        dateTo: this.formatBookDate(dateTo),
      }),
    };
  }
}
