import { convertToParamMap, ParamMap, Params } from '@angular/router';
import { BehaviorSubject, Observable, of } from 'rxjs';

export class ActivatedRouteStub {
  private _paramMap!: ParamMap;
  private subject = new BehaviorSubject<ParamMap>({} as ParamMap);
  /* eslint-disable @typescript-eslint/member-ordering */
  paramMap: Observable<ParamMap> = this.subject.asObservable();
  parent?: ActivatedRouteStub;
  /* eslint-enable @typescript-eslint/member-ordering */

  constructor(initialParams?: Params) {
    this.setParamMap(initialParams);
  }

  setParamMap(params: Params = {}, parent?: boolean) {
    const paramMap = convertToParamMap(params);
    this._paramMap = paramMap;
    if (parent) {
      this.parent = new ActivatedRouteStub();
      this.parent.paramMap = of(paramMap);
    }
    this.subject.next(paramMap);
  }
}
