import { Directive, Input } from '@angular/core';

import { AccountStatementBasePropertiesService } from '../../models/account-statement-base-properties-service.model';
import { PaginationType } from '../../models/pagination-type.model';
import { SortDirection } from '../../models/sort-direction.model';

// @dynamic
@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class AccountStatementBaseWidgetComponent<T extends AccountStatementBasePropertiesService> {
  /* eslint-disable @angular-eslint/no-input-rename */
  /**
   * Number of statements per page
   */
  @Input('pageSize') set pageSizeValue(value: number | undefined) {
    this.propertiesService.setPageSize(value);
  }

  /**
   * Sorting order (date, description, category)
   */
  @Input('sortingOrder') set sortingOrderValue(value: string | undefined) {
    this.propertiesService.setSortingOrder(value);
  }

  /**
   * Sorting direction (ASC, DESC)
   */
  @Input('sortingDirection') set sortingDirectionValue(value: SortDirection | undefined) {
    this.propertiesService.setSortingDirection(value);
  }

  /**
   * Flag to show/hide categories
   */
  @Input('showCategory') set showCategoryValue(value: boolean | undefined) {
    this.propertiesService.setShowCategory(value);
  }

  /* eslint-enable @angular-eslint/no-input-rename */

  /**
   * Notification dismiss time in seconds
   */
  @Input() set notificationDismissTime(value: number | undefined) {
    this.propertiesService.setNotificationDismissTime(value);
  }
  /**
   * Relative filter datepicker limit (months)
   */
  @Input() set relativeDateLimit(value: number | undefined) {
    this.propertiesService.setRelativeDateLimit(value);
  }
  /**
   * Absolute filter datepicker limit (yyyy-mm-dd)
   */
  @Input() set absoluteDateLimit(value: string | undefined) {
    this.propertiesService.setAbsoluteDateLimit(value);
  }
  /**
   * Pagination type (PAGINATION or LOAD_MORE)
   */
  @Input() set paginationType(value: PaginationType | undefined) {
    this.propertiesService.setPaginationType(value);
  }

  protected constructor(protected readonly propertiesService: T) {}
}
