import { ChangeDetectionStrategy, Component, Input, Renderer2 } from '@angular/core';
import { AccountStatementDomService } from '../../services/account-statement-dom-service';

@Component({
  selector: 'bb-account-statement-view-online',
  templateUrl: './account-statement-view-online.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountStatementViewOnlineComponent {
  @Input() href = '/';

  @Input() describedByElementId = '';

  constructor(private readonly domService: AccountStatementDomService, private readonly renderer: Renderer2) {}

  viewOnline() {
    this.domService.simulateLinkClick(
      {
        target: '_blank',
        href: this.href,
      },
      this.renderer,
    );
  }
}
